.contact-cont{
    width: 100%;
    margin-top: 40px;
}
.contact-cont .heading{
    width: 85%;
    margin: auto;
    font-family: "Tenor Sans";
    font-size: 1.5rem;
    padding-bottom: 20px;
    border-bottom: solid 2px #cacaca;
    font-weight: bolder;
}
.contact-cont .formandimg{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 85%;
    margin: auto;
    padding: 20px 0px;
}
.formandimg .img-cont{
    width: 40%;
    height: 60vh;
    opacity: 0.3;
    background-size: 10px 10px;
    border-radius: 30px;
}
.img-cont .pattern{
    width: 100%;
}

.formandimg .contactForm{
    width: 55%;
}

.contactForm .input-field{
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 15px;
}

.input-field input{
    padding: 5px 10px;
    font-size: 1.1rem;
    border-radius: 10px;
    border: solid 1px #000;
}
.input-field input:focus{
    outline: none;
}
.input-field textarea{
    padding: 10px;
    border-radius: 10px;
    font-size: 1.1rem;
    border: solid 1px #000;
}
.input-field textarea:focus{
    outline: none;
}

@media screen and (max-width: 768px) {
    .formandimg .img-cont{
        width: 0;
        display: none;
    }
    .formandimg .contactForm{
        width: 100%;
    }
    
}